// TypeScript

// 
export interface Note {
  name: string;        // : "C", "F#"
  octave: number;      // : 4
  frequency: number;   // Hz
}

// 
export interface Chord {
  root: string;           // 
  quality: string;        // : "major", "minor7"
  extensions: string[];   // : ["9", "13"]
  bass?: string;          // 
}

// 
export interface Voicing {
  notes: Note[];                    // 
  type: 'close' | 'open' | 'drop2' | 'drop3';  // 
  position: number;                 // 
}

// 
export interface ChordProgression {
  chords: string[];      // 
  key: string;           // 
  timeSignature: string; // : "4/4"
}

// 
export interface VoicingResult {
  chord: string;     // 
  voicing: Voicing;  // 
  position: number;  // 
}

