import React, { useState, useEffect, useCallback } from 'react';
// React: UI
// useState: Hook
// useEffect: DOMHook
// useCallback: Hook

import styled from 'styled-components';
// styled-components: CSS-in-JS
// JavaScriptCSS

// =====  =====
// 
import ChordInput from './components/ChordInput';        // 
import VoicingDisplay from './components/VoicingDisplay'; // 
import ControlPanel from './components/ControlPanel';    // 
import PianoRoll from './components/PianoRoll';          // 

// =====  =====
import { Voicing } from './types/music';                 // 
import { generateSmoothVoicings } from './utils/musicTheory'; // 
import { audioPlayer } from './utils/audioPlayer';       // 

// =====  =====
// styled-componentsCSS-in-JS
//
// styled-components
// 1. CSS
// 2. JavaScriptCSS
// 3. 
// 4. 
// 5. CSS Modules

// 
// styled.divdivReact
const AppContainer = styled.div`
  min-height: 100vh;  // viewport height
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); // 135
  padding: 2rem;      // 2rem
`;

// 
// 
const MainContent = styled.div`
  max-width: 1200px;  // 1200px
  margin: 0 auto;     // 0auto
`;

// 
// 
const Header = styled.header`
  text-align: center; // 
  margin-bottom: 3rem; // 3rem
`;

// 
// 
const Title = styled.h1`
  font-size: 3rem;    // 48px
  color: white;       // 
  margin: 0 0 1rem 0; // 001rem0
  font-weight: 700;   // bold
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3); // 
`;


// 2
// CSS Grid
const ContentGrid = styled.div`
  display: grid;                    // CSS Grid
  grid-template-columns: 1fr 1fr;   // 21fr = 1 fraction
  gap: 2rem;                        // 
  margin-bottom: 2rem;              // 

  // 
  // 768px
  @media (max-width: 768px) {
    grid-template-columns: 1fr;     // 1
  }
`;

const PianoRollSection = styled.div`
  grid-column: 1 / -1;
  margin-bottom: 2rem;
`;

const LeftColumn = styled.div`
  display: flex;
  flex-direction: column;
  gap: 2rem;
`;

const RightColumn = styled.div`
  display: flex;
  flex-direction: column;
  gap: 2rem;
`;

const LoadingOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
`;

const LoadingSpinner = styled.div`
  width: 50px;
  height: 50px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #4a90e2;
  border-radius: 50%;
  animation: spin 1s linear infinite;

  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
`;

const LoadingText = styled.p`
  color: white;
  font-size: 1.1rem;
  margin-top: 1rem;
`;

// =====  =====
function App() {
  // ===== React Hooks =====
  // useStateReactHook
  // : const [, ] = useState()
  // 
  
  // 
  // 
  const [chordProgression, setChordProgression] = useState<string[]>([]);
  
  // 
  // 
  const [voicings, setVoicings] = useState<Voicing[]>([]);
  
  // 
  // 
  const [isLoading, setIsLoading] = useState(false);
  
  // 
  // 
  const [isPlaying, setIsPlaying] = useState(false);
  
  // 
  // Web Audio API
  const [isAudioInitialized, setIsAudioInitialized] = useState(false);
  
  // =====  =====
  // close, open, drop2, drop34
  const [voicingType, setVoicingType] = useState<'close' | 'open' | 'drop2' | 'drop3'>('close');
  const [tempo, setTempo] = useState(120);  // BPMBeats Per Minute
  const [volume, setVolume] = useState(12); // 0-24

  // =====  =====
  // 
  
  // 
  // Web Audio API
  const handleInitializeAudio = useCallback(async () => {
    try {
      // Tone.js
      // 
      await audioPlayer.initialize();
      setIsAudioInitialized(true); // 
      // -500
      audioPlayer.setVolume(volume - 50);
      // 
      audioPlayer.setTempo(tempo);
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  }, [volume, tempo]);

  // 
  // 
  const handleGenerateVoicing = useCallback(async () => {
    // 
    if (chordProgression.length === 0) return;
    
    setIsLoading(true); // 
    
    try {
      // API
      // generateSmoothVoicings
      const generatedVoicings = generateSmoothVoicings(chordProgression, voicingType);
      setVoicings(generatedVoicings); // 
    } catch (error) {
      console.error(':', error);
      alert('');
    } finally {
      setIsLoading(false); // 
    }
  }, [chordProgression, voicingType]);

  // 
  // 
  const handlePlayVoicing = useCallback((voicing: Voicing) => {
    // 
    if (!isAudioInitialized) {
      alert('');
      return;
    }
    
    // 2
    audioPlayer.playVoicing(voicing, '2n');
  }, [isAudioInitialized]);

  // 
  const handlePlayProgression = useCallback(() => {
    if (!isAudioInitialized) {
      alert('');
      return;
    }
    
    if (voicings.length === 0) return;
    
    setIsPlaying(true);
    audioPlayer.playProgression(voicings, tempo);
    
    // 
    setTimeout(() => {
      setIsPlaying(false);
    }, (voicings.length * 2 * 60 / tempo) * 1000);
  }, [isAudioInitialized, voicings, tempo]);

  // 
  const handleStopPlayback = useCallback(() => {
    audioPlayer.stopAll();
    setIsPlaying(false);
  }, []);


  // 
  const handleTempoChange = useCallback((newTempo: number) => {
    setTempo(newTempo);
    if (isAudioInitialized) {
      audioPlayer.setTempo(newTempo);
    }
  }, [isAudioInitialized]);

  // 
  const handleVolumeChange = useCallback((newVolume: number) => {
    setVolume(newVolume);
    if (isAudioInitialized) {
      audioPlayer.setVolume(newVolume - 50); // -50 to 0 range
    }
  }, [isAudioInitialized]);

  // ===== useEffect =====
  // 
  // [voicingType]
  useEffect(() => {
    // 
    if (chordProgression.length > 0 && voicings.length > 0) {
      handleGenerateVoicing();
    }
  }, [voicingType, chordProgression.length, voicings.length, handleGenerateVoicing]);

  // ===== JSX =====
  // returnJSXUI
  return (
    <AppContainer>
      <MainContent>
        {/*  */}
        <Header>
          <Title></Title>
        </Header>

        {/* 2 */}
        <ContentGrid>
          {/*  */}
          <LeftColumn>
            {/*  */}
            <ChordInput
              onChordProgressionChange={setChordProgression} // 
              onGenerateVoicing={handleGenerateVoicing}      // 
              isLoading={isLoading}                          // 
            />
            {/*  */}
            <ControlPanel
              voicingType={voicingType}                      // 
              onVoicingTypeChange={setVoicingType}           // 
              tempo={tempo}                                  // 
              onTempoChange={handleTempoChange}              // 
              volume={volume}                                // 
              onVolumeChange={handleVolumeChange}            // 
              onInitializeAudio={handleInitializeAudio}      // 
              isAudioInitialized={isAudioInitialized}        // 
            />
          </LeftColumn>

          {/*  */}
          <RightColumn>
            {/*  */}
            <VoicingDisplay
              voicings={voicings}                            // 
              onPlayVoicing={handlePlayVoicing}              // 
              onPlayProgression={handlePlayProgression}      // 
              onStopPlayback={handleStopPlayback}            // 
              isPlaying={isPlaying}                          // 
            />
          </RightColumn>
        </ContentGrid>

        {/*  */}
        <PianoRollSection>
          <PianoRoll voicings={voicings} />
        </PianoRollSection>

        {/*  */}
        {isLoading && (
          <LoadingOverlay>
            <div style={{ textAlign: 'center' }}>
              <LoadingSpinner />
              <LoadingText>...</LoadingText>
            </div>
          </LoadingOverlay>
        )}
      </MainContent>
    </AppContainer>
  );
}

// 
// import App from './App'
export default App;
