// React
// React: 
import React from 'react';

// ReactDOM: DOM
// React 18 createRoot API 
import ReactDOM from 'react-dom/client';

// 
import './index.css';

// 
import App from './App';

// DOMReact
// document.getElementById('root'): HTML<div id="root">
// as HTMLElement: TypeScript
const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);

// ReactDOM
// React.StrictMode: 
// 
// - API
// - 
// - 
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);
